.landingNavBar_btn__2DBqy {
    box-sizing: inherit;
    transition-property: all;
    transition-duration: .6s;
    transition-timing-function: ease;
    color: #fff;
    cursor: pointer;
    text-decoration: none;
    text-transform: uppercase;
}

@media(min-width: 400px) {

    .landingNavBar_btn2__nElCL {
        letter-spacing: 0;
    }

    .landingNavBar_btn2__nElCL:hover,
    .landingNavBar_btn2__nElCL:active {
        letter-spacing: 5px;
    }

    .landingNavBar_btn2__nElCL:after,
    .landingNavBar_btn2__nElCL:before {
        -webkit-backface-visibility: hidden;
                backface-visibility: hidden;
        bottom: 0;
        content: " ";
        display: block;
        margin: 0 auto;
        position: relative;
        transition: all 280ms ease-in-out;
        width: 0;
    }

    .landingNavBar_btn2__nElCL:hover:after,
    .landingNavBar_btn2__nElCL:hover:before {
        -webkit-backface-visibility: hidden;
                backface-visibility: hidden;
        border-color: #fff;
        transition: width 350ms ease-in-out;
        width: 70%;
    }

    .landingNavBar_btn2__nElCL:hover:before {
        bottom: auto;
        top: 0;
        width: 70%;
    }
}

